import React from 'react';
import './hero-carousel.css';
import BillMaherHero from '../../images/hero/bill-maher-hero-01.jpg';

export default class HeroCarouselPanel extends React.Component
{
	constructor( props )
	{
		super( props );
		this.Data = props.data;
		this.Title = props.title;
        return;
	};
	render()
	{	
		return (
			<div className="hero-carousel-panel">
				<div className="hero-content-panel">
					<div className="hero-card-panel">
						<img className="hero-content-image" src={BillMaherHero} alt="testing" />
						<div>Real Time with Bill Maher</div>
						<div>Season 15, Episode 10</div>
					</div>
					<div className="hero-card-panel">
						<img className="hero-content-image" src={BillMaherHero} alt="testing" />
						<div>Real Time with Bill Maher</div>
						<div>Season 15, Episode 10</div>
					</div>
					<div className="hero-card-panel">
						<img className="hero-content-image" src={BillMaherHero} alt="testing" />
						<div>Real Time with Bill Maher</div>
						<div>Season 15, Episode 10</div>
					</div>
				</div>
				<div className="hero-control-overlay-panel">
					<div className="slider-button-panel">
						<div className="slider-button">&uarr;</div>
					</div>
					<div className="slider-center-column">
						<div className="silder-feature-panel">Play</div>
						<div className="slider-nav-panel">*******</div>
					</div>
						<div className="slider-button-panel slider-button-right">
							<div className="slider-button">&darr;</div>
						</div>
				</div>
			</div>
		);
	};
};
export
{
	HeroCarouselPanel as HeroCarousel
};